import React from "react"
import PaletteIcon from "./color-palette.png"
import RoadTripIcon from "./road-trip.png"

const Projects = () => {
	return (
		<div className="ui stackable raised cards">
			<div className="ui centered card">
				<div className="content">
					<div className="header">
						<img
							src={PaletteIcon}
							style={{ height: "24px", width: "24px", marginRight: "10px" }}
						/>
						Colorati
					</div>
					<div className="left aligned description">
						A web app for color palettes built entirely using React
						{/* <ul>
							<li>Create your own color palettes.</li>
							<li>Rearrange color boxes in your palette using drag-n-drop.</li>
							<li>
								View your own color palettes as well as a few pre-loaded ones.
							</li>
							<li>
								Click on a color to copy the color's code in your chosen format.
							</li>
							<li>Choose from 9 different shades for every color.</li>
							<li>Choose between HEX, RGB and RGBA color formats.</li>
						</ul> */}
					</div>
				</div>
				<div className="extra content">
					<div className="ui two buttons">
						<a
							className="ui basic blue button"
							href="https://github.com/flashkicker/colorati"
							target="_blank"
							rel="noopener noreferrer"
						>
							Github
						</a>
						<a
							className="ui basic black button"
							href="https://colorati.herokuapp.com/"
							target="_blank"
							rel="noopener noreferrer"
						>
							Demo
						</a>
					</div>
				</div>
			</div>
			<div className="ui centered card">
				<div className="content">
					<div className="header">
						<img
							src={RoadTripIcon}
							style={{ height: "24px", width: "24px", marginRight: "10px" }}
						/>
						Trippr
					</div>
					<div className="left aligned description">
						A web app built on the MERN stack that lets you create, view, save
						and share your personalized road trips.
					</div>
				</div>
				<div className="extra content">
					<div className="ui two buttons">
						<a
							className="ui basic blue button"
							href="https://github.com/flashkicker/Trippr"
							target="_blank"
							rel="noopener noreferrer"
						>
							Github
						</a>
						<a
							className="ui basic black button"
							href="https://calm-badlands-76250.herokuapp.com"
							target="_blank"
							rel="noopener noreferrer"
						>
							Demo
						</a>
					</div>
				</div>
			</div>
		</div>
	)
}

export default Projects
